.featured {
    width: 100%;
    padding: 8rem 0;
    background-color: var(--background);
   

}

.featured .container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    justify-content: center;
    align-items: center;
    padding: 1rem;
    gap: 3rem;
}


.chart-title {
    color: #05386B;
    text-align: center;
}



.featured .card {
    display: flex;
    flex-direction: column;
    padding: 12px 22px;
    margin: auto;
    width: 300px;
    height: 300px;
    border-radius: 24px;
    border: 1px solid lightgray;
    box-shadow: rgb(0 0 0 / 12%) 0px 8px 24px;
    text-align: center;
}

.featured .card:hover {
    box-shadow: rgb(0 0 0 / 20%) 0px 8px 24px;
}

.featured .card .top {
    display: flex;
    flex-direction: column;

}

.featured img {
    width: 150px;
    margin: auto;
    padding: .2rem 0;
}
h5 {
    font-size: 1.5rem;
}

 h5 > p {
    font-size: 1.2rem;
}

.featured .icon {
    font-size: 35px;
    margin: auto;
    padding-top: 6px;
    justify-content: center;
    align-items: center;
}

.featured span {
    font-size: 20px;
}

.green {
    color: green;
}
.red {
    color: #ff0000;
}

@media screen and (max-width: 940px) {
   
    .featured .container .card {
        width: 100%;
        margin: .5rem;
     
    }
    
    .featured .left .btn {
        align-self: center;
    }
 
}

@media screen and (max-width: 478px) {
    .featured .container .card {
        width: 100%;
    }
}