.container.contact_container {
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    gap: 12%;
    
}

.contact_options {
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}
.contact_option {
    background: #8EE4AF;
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: all 400ms ease;
    margin: .7rem 0;
}
.contact_option:hover {
    background: transparent;
    border-color: rgba(77, 181, 255, 0.4);
}
.contact_icon {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}
.contact_option a {
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
}
.contact_option h5 {
    color: rgba(255, 255, 255, 0.6);
}

/* Form */
form {
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
    margin: 5rem 0;
    
}
input, textarea {
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid gray;
    resize: none;
    color: black;
}
.email3 {
    font-size: 14px;
}


.areatext {
    background-color: transparent;
    border: 2px solid gray;
}
/* Media Query (Med Device) */
@media screen and (max-width: 1024px) {
    .container.contact_container {
        grid-template-columns: 1fr;
        gap: 2rem;
    }
}

/* Media Query (Small Device) */
@media screen and (max-width: 600px) {
    .container.contact_container {
        width: 90%;
    }
}