footer {
    background: #05386B;
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    
}

footer a {
    color: white;
}
.footer_logo {
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}

.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.footer_socials {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 4rem;
}
.footer_socials a {
    background: gray;
    color: white;
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}
.footer_socials a:hover {
    background: transparent;
    color: gray;
    border-color: gray;
}

.footer_copyright {
    margin-bottom: 4rem;
    color: var(--color-bg);
}

/* Media Query (Small Device) */
@media screen and (max-width: 600px) {
    .permalinks {
        flex-direction: column;
        gap: 1.5rem;
    }
    .footer_socials {
        margin-bottom: 2.6rem;
    }
}