.notes-list {
    display: grid;
    grid-gap: 1rem;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    margin-bottom: 6rem;
}

.note.new {
    background-color: #05386B;
}
.note-footer small {
    color: gray;
}

textarea {
    border: none;
    resize: none;
    background-color: #05386B;
    color: black;
}

textarea:focus {
    outline: none;
}

.save {
    background-color: #8EE4AF;
    border: none;
    border-radius: 15px;
    padding: 5px 10px 5px 10px;
}

.save:hover {
    background-color: #ededed;
    cursor: pointer;
}

.note {
    background-color: #fef68a;
    border-radius: 10px;
    padding: 1rem;
    min-height: 170px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    white-space: pre-wrap;
}

.note-footer {
    display: flex;
    align-items: center;
     justify-content: space-between;
}

.container1 {
    max-width: 960px;
    margin: 0 auto;
    padding: 0 15px;
}

.delete-icon:hover {
    color: red;
    cursor: pointer;
}

.note-title {
    text-align: center;
    color: gray;
    font-size: 3rem;
    margin-bottom: 2rem;
}

