@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;800&display=swap');

:root {
  --primary: #118c4f;
  --background: #379683;

}
.primary {
  color: var(--primary);
}


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}
ul {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: white;
}

.container {
  max-width: 1240px;
  margin: auto;
}

body {
  font-family: 'Poppins', sans-serif;
  background-color: #FBEEC1;
}

h1 {
  font-size: 3.5rem;
  line-height: 1.2;
}

h2 {
  font-size: 2.4rem;
  line-height: 1.2;
}

h5 {
  font-size: 1.1rem;
  line-height: 1.2;
}
p {
  font-size: 1.2rem;
}

.btn {
  padding: 14px 32px;
  border: 1px solid gray;
  background-color: #05386B;
  color: #fff;
  border-radius: 8px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
}

.btn:hover {
  box-shadow: rgb(0 0 0 / 15%) 0px 8px 24px;
  background: #699372;
}

input {
  padding: 12px 32px;
  border: 1px solid gray;
  background: transparent;
  border-radius: 8px;
  font-size: 1rem;
  margin-right: .8rem;
  font-family: 'Poppins', sans-serif;
}

@media screen and (max-width:768px) {
  h1 {
    font-size: 2.1rem;
    line-height: 1.2;
  }
  h2 {
    font-size: 1.5rem;
    line-height: 1.2;
  }
  h5 {
    font-size: 1.1rem;
    line-height: 1.2;
  }
  p {
    font-size: 1rem;
  }

  .btn {
    width: 100%;
    margin: 1rem 0;
  }
  input {
    width: 100%;
  }
}